export default {
	data() {
		return {
			// 已捕捉
			catched:false,
			//图鉴类型
			dexType:'undefined',
			// 半球（n or s)
			// hemisphere:'sh',
			// index:0,
			// inspectData:'undefined',
			//图鉴详细数据
			info:[]
		};
	},
	onLoad(option){
		//接收从列表页传来的图鉴类型
		this.dexType = option.dexType;
		console.log("详情页图鉴类型："+this.dexType)
		//接收从列表页传过来的详细信息
		this.info = JSON.parse(decodeURIComponent(option.item));			
	}
}
